import Vue from 'vue';
import {_} from 'underscore';

export default {
    setUserInfo(state, info) {
        if (!_.isEqual(state.userinfo, info)) {
            Vue.set(state, 'userinfo', info);
        }
    },
    setSource(state, data) {
        Vue.set(state, 'source', data);
    },
    addProps(state, data) {
        data.is_remove = false;
        state.template.props.push(data);
    },
    removeProps(state, index) {
        let prop = state.template.props[index];
        state.template.source.forEach((source) => {
            source.action.forEach((action) => {
                action.options.forEach((options) =>{
                    if (options.prop && options.prop === prop) {
                        Vue.set(options, 'prop', null);
                    }
                });
            });
        });
        state.template.props.splice(index, 1);
    },
    addSource(state, {item1, item2}) {
        item2 = JSON.parse(JSON.stringify(item2))
        let model = state.template.source.find((val) => {
            return val.name == item1.name;
        });
        if (!model) {
            model = {
                name: item1.name,
                title: item1.title,
                action: []
            };
            state.template.source.push(model);
        }

        let action = model.action.find((val) => {
            return val.name == item2.name;
        });
        // if (!action) {
            Vue.set(item2, 'is_options', false);
            Vue.set(item2, 'is_remove', false);
            item2.options.forEach(e => {
                if (e.prop) {
                    e.prop = state.template.props.find((val) => {
                        return val.name == e.prop.name;
                    });
                }
                Vue.set(e, 'is_props', false);
            });
            model.action.push(item2);
        // }
    },
    setTemplateName(state, name) {
        state.template.name = name;
    },
    setTemplateCode(state, code) {
        state.template.code = code;
    },
    setCompoment(state, {info}) {
        let props = info.props;
        let source = info.source;
        // state.template.name = info.name;
        state.template.code = info.code;
        
        for (let key in props) {
            this.commit('addProps', { name: key, value: props[key].value, type: props[key].type });
        }

        let findProps = function(name) {
            return state.template.props.find((val) => {
                return val.name == name;
            });
        }

        for (let value of source) {
            let model = state.source.find((data) => {
                return data.name == value.name;
            });
            let action = model.action.find((data) => {
                return data.action == value.action;
            });
            action.name = value.param;
            for (let key in value.options) {
                let options = action.options.find((data) => {
                    return data.name == key;
                });
                if (typeof value.options[key] == 'object') {
                    options.value = value.options[key].value;
                    options.prop = findProps(value.options[key].prop);
                } else {
                    options.value = value.options[key];
                }
            }
            
            this.commit('addSource', {item1: model, item2: action});
        }
    }
}


