import ibutton from './ibutton.vue'
import iinput from './iinput.vue'
import icontent from './icontent.vue'

const components = [
    ibutton,
    iinput,
    icontent
]

function install(Vue) {
    components.map(component => Vue.component(component.name, component));
}

export default {
    install
}


