export default {
    parseSource(state) {
        let data = [];
        for (let value of state.template.source) {
            for (let _value of value.action) {
                let params = {};
                for (let __value of _value.options) {
                    if (__value.prop) {
                        params[__value.name] = { prop: __value.prop.name, value: __value.value };
                    } else {
                        params[__value.name] = __value.value;
                    }
                }
                
                data.push({
                    name: value.name,
                    action: _value.action,
                    param: _value.name,
                    options: params
                })
            }
        }
        return data;
    },
    parseProps(state) {
        let data = {};
        for (let value of state.template.props) {
            data[value.name] = {
                type: value.type,
                value: value.value
            };
        }
        return data;
    }
}


