import Vue from 'vue';

export default {
    login({ commit, dispatch, state }, info) {
        return new Promise((resolve, reject) => {
            Vue.axios.post('/login/loginApi', info).then(({ data }) => {
                if (data.err) {
                    // reject(1);
                    resolve(data)
                } else {
                    // commit('setUserInfo', data.data);
                    resolve(data)
                }
            });
        });
    },
    getSource({ commit }) {
        return new Promise((resolve, reject) => {
            Vue.axios.get('/admin/getSource').then(({ data }) => {
                if (data.err) {
                    // reject(1);
                    resolve(data)
                } else {
                    commit('setSource', data);
                    resolve(data)
                }
            });
        });
    },
    getComponent({ commit, dispatch, state }, info) {
        return new Promise((resolve, reject) => {
            Vue.axios.get('/admin/getComponent', { params: info }).then(({ data }) => {
                if (data.err) {
                    // reject(1);
                    resolve(data)
                } else {
                    commit('setCompoment', {info: data.data});
                    resolve(data)
                }
            });
        });
    }
}


