<template>
  <button class="btn" :class="{'btn-fill': fill != 'unfill'}"><slot></slot></button>
</template>

<script>
export default {
  name: 'ibutton',
  props:{
    fill: {
      type: String,
      default: 'unfill'
    }
  }
}
</script>


