<template>
    <div>
        
        <div class="item-line item-lg">
            <div class="item-title">名称</div>
            <div class="item-input"><input type="text" class="input input-lg" placeholder="请输入账号" v-model="name"></div>
        </div>
        <div class="source_nav">
            数据<div @click="addProps" class="btn btn_add_source btn_right">添加</div>
        </div>
        
        <div class="source_nav">
                <div v-show="!props.length" class="source_empty">没有添加数据</div>
                <div v-show="props.length" class="flex-box source_item">
                    <div class="flex-delete"></div>
                    <div class="flex-1">名称</div>
                    <div class="flex-1">初始值</div>
                    <div class="flex-2">赋值类型</div>
                </div>
                <div class="source_body source_item flex-box" v-for="(item, key) in props" :key="key">
                    <div class="btn_delete mar" @click="item.is_remove = true">-</div>
                    <div class="flex-1">
                        <input type="text" class="input source_name" v-model="item.name">
                    </div>
                    <div class="flex-1">
                        <input type="text" class="input source_name" v-model="item.value">
                    </div>
                    <div class="flex-2">
                        <label><input class="input-radio" type="radio" v-model="item.type" value="value">VALUE</label>
                        <label><input class="input-radio" type="radio" v-model="item.type" value="get">GET</label>
                        <label><input class="input-radio" type="radio" v-model="item.type" value="post">POST</label>
                    </div>
                    <base-modal :show="item.is_remove" @click.native="item.is_remove=!item.is_remove">
                        <div slot="title" class="remove_title">
                            删除数据源
                        </div>
                        <div slot="content">
                            <div class="remove_msg">
                                数据源名称：{{item.name}}
                            </div>
                            <div class="remove_btn btn btn-danger btn-fill btn-lg" @click="removeProps(key)">确认</div>
                        </div>
                    </base-modal>
                </div>
        </div>
        <div class="source_nav">
            数据源<div @click="addSource" class="btn btn_add_source btn_right">添加</div>
        </div>
        <div class="source_nav">
            <div class="source_list">
                <div v-show="!source.length" class="source_empty">没有添加数据源</div>
                <div class="source_body" v-for="(item, key) in source" :key="key">
                    <div class="source_list">
                        <div class="source_item" v-for="(_item, _key) in item.action" :key="_key">
                            <div class="item_title">
                                <span class="btn_delete" @click="_item.is_remove = true">-</span>
                                <span>名称</span>
                                <input class="input source_name" v-model="_item.name" type="text">
                                <span>{{item.title}} - {{_item.title}} </span>
                                <span class="btn btn_right" @click="_item.is_options = !_item.is_options">配置</span>
                            </div>
                            <collapse-transition>
                                <div v-show="_item.is_options" class="item_setting">
                                    <div class="source_empty" v-show="!_item.options || !_item.options.length">该数据无须置</div>
                                    <div v-for="param in _item.options" :key="param.name">
                                        <div class="item-line">
                                            <div class="item-title">{{param.title}}</div>
                                            <div class="item-input">
                                                <div v-if="param.prop">
                                                    {{param.prop.name}} - {{param.prop.value || '空'}} - {{param.prop.type}}
                                                </div>
                                                <input v-else type="text" class="input" v-model="param.value" :placeholder="param.name">
                                            </div>
                                            <div class="btn btn-sm btn-fill props" @click="param.is_props=true">数据</div>
                                            <div class="show_explain" @click="showExplain(param.explain)">说明</div>
                                        </div>
                                        <base-modal :show="param.is_props" @click.native="param.is_props=!param.is_props">
                                            <div slot="title">
                                                选择数据
                                            </div>
                                            <div slot="content">
                                                <div class="source_nav">
                                                    <div v-show="!props.length" class="source_empty">没有添加数据</div>
                                                    <div class="use_props">
                                                        <div class="btn btn-sm" @click="useProps(param);param.is_props=false;">值:{{param.value}}</div>
                                                        <div class="btn btn-sm" v-for="(item, key) in props" :key="key" @click="useProps(param, item);param.is_props=false;">
                                                            {{item.name}}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </base-modal>
                                    </div>
                                </div>
                            </collapse-transition>
                            <base-modal :show="_item.is_remove" @click.native="_item.is_remove=!_item.is_remove">
                                <div slot="title" class="remove_title">
                                    删除数据源[{{item.title}} - {{_item.title}}]
                                </div>
                                <div slot="content">
                                    <div class="remove_msg">
                                        数据源名称：{{_item.name}}
                                    </div>
                                    <div class="remove_btn btn btn-danger btn-fill btn-lg" @click="removeModal(key, _key)">确认</div>
                                </div>
                            </base-modal>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- <div class="item-line item-lg">
            <div class="item-title">标题</div>
            <div class="item-input"><input type="text" class="input input-lg" placeholder="请输入账号"></div>
        </div>
        <div class="item-line item-lg">
            <div class="item-title">参数</div>
            <div class="item-input"><input type="text" class="input input-lg" placeholder="请输入账号"></div>
        </div> -->
        <!-- <div class="item-line item-lg">
            <div class="item-title">模板</div>
            <div class="item-input" @click="editCode">编辑</div>
        </div> -->
        <div class="codemirror_editor" :class="{codemirror_editor_fullscreen: is_fullscreen}">
            <div class="codemirror_nav">
                <div class="btn_nav">模板编辑</div>
                <div class="btn_nav" @click="undo">撤销</div>
                <div class="btn_nav" @click="redo">恢复</div>
                <div class="flex"></div>
                <div class="btn_nav max_show" @click="fullscreen">
                    <i class="icon-svg" :style="{ backgroundImage: 'url(' + require('../../assets/svg/maxshow.svg') + ')' }"></i>
                </div>
            </div>
            <codemirror class="codemirror" v-model="code"
            :options="{
                mode: 'php',
                lineNumbers: true,
                smartIndent: true,
                theme:'monokai',
                lineWrapping: 'wrap'
            }" ref="myEditor"/>
        </div>
        <div @click="save" class="btn-save btn btn-fill btn-lg">保存</div>
        <base-modal :show="showModal" @click.native="showModal=!showModal">
            <div slot="title">
                选择数据源
            </div>
            <div slot="content">
                <template-source @hide-model="showModal=!showModal"/>
            </div>
        </base-modal>
        
        <base-modal :show="showOptionsModal" @click.native="showOptionsModal=!showOptionsModal">
            <div slot="title" class="explain_title">
                参数说明
            </div>
            <div slot="content" class="explain_box">
                <div v-html="explain"></div>
            </div>
        </base-modal>
        
        <base-modal :show="commonModal" @click.native="commonModal=!commonModal">
            <div slot="title" class="explain_title">
                <div v-html="commonModalTitle"></div>
            </div>
            <div slot="content" class="explain_box">
                <div v-html="commonModalContent"></div>
            </div>
        </base-modal>
    </div>
</template>

<script>
import BaseModal from "@/components/common/BaseModal.vue";
import TemplateSource from "@/components/template/Source.vue";
import CollapseTransition from "@/components/common/CollapseTransition.vue";
import { codemirror, CodeMirror } from 'vue-codemirror-lite';
import 'codemirror/theme/monokai.css';
import 'codemirror/keymap/sublime.js';
import 'codemirror/mode/php/php.js';

export default {
    data() {
        return {
            // namespace: '\\adsda\\asdasd\asda22\\22222',
            showModal: false,
            showOptionsModal: false,
            is_fullscreen: false,
            explain: '',
            // name: '',
            // code: ``,
            commonModal: false,
            commonModalTitle: '',
            commonModalContent: ''
        };
    },
    components: {
        BaseModal,
        TemplateSource,
        CollapseTransition,
        codemirror
    },
    computed: {
        source() {
            return this.$store.state.template.source;
        },
        props() {
            return this.$store.state.template.props;
        },
        code: {
            get() {
                return this.$store.state.template.code;
            },
            set(value) {
                this.$store.commit('setTemplateCode', value);
            }
        },
        editor() {
            return this.$refs.myEditor.editor
        },
        namespace() {
            return this.$route.query.namespace ? this.$route.query.namespace : '\\';
        },
        name: {
            get() {
                return this.$store.state.template.name;
            },
            set(value) {
                this.$store.commit('setTemplateName', value);
            }
        }
    },
    methods: {
        addSource() {
            this.showModal = true;
        },
        addProps() {
            this.$store.commit('addProps', {
                'name': 'data' + this.props.length,
                'value': '',
                'type': 'value'
            });
        },
        showExplain(explain) {
            this.explain = explain;
            this.showOptionsModal = true;
        },
        removeProps(index) {
            this.$store.commit('removeProps', index);
        },
        removeModal(key1, key2) {
            this.source[key1].action.splice(key2, 1);
            if (this.source[key1].action.length == 0) {
                this.source.splice(key1, 1);
            }
            // console.log(key1, key2, this.source[key1].action[key2]);
        },
        useProps(param, item) {
            if (param && item) {
                this.$set(param, 'prop', item);
            } else {
                this.$set(param, 'prop', null);
            }
            console.log(this.$store.state.template.source);
        },
        editCode() {
            console.log(CodeMirror);
        },
        fullscreen() {
            this.is_fullscreen = !this.is_fullscreen;
        },
        undo() {
            this.editor.undo();
        },
        redo() {
            this.editor.redo();
        },
        save() {
            let data = {
                name: this.name,
                props: JSON.stringify(this.$store.getters.parseProps),
                source: JSON.stringify(this.$store.getters.parseSource),
                code: this.code
            };
            // console.log(data);return;
            this.$axios.post('/admin/componentAdd?namespace=' + this.namespace, data).then(({data}) => {
                if (data.err) {
                    this.commonModalTitle = '保存失败';
                    this.commonModalContent = data.msg;
                    this.commonModal = true;
                } else {
                    location.href = data.href;
                }
            });
        }
    },
    async created() {
        await this.$store.dispatch('getSource');
        console.log(1);
        if (this.$route.query.component) {
            console.log(this.$route.query.component);
            this.$store.commit('setTemplateName', this.$route.query.component);
            this.$store.dispatch('getComponent', {
                namespace: this.$route.query.namespace,
                component: this.$route.query.component
            })
        }
    }
};
</script>

<style scoped>
.source_nav {
    border-bottom: .05rem solid #eee;
    padding: .5rem;
    line-height: 1.5rem;
    background: #FFF;
    overflow: hidden;
}
.source_nav .btn_right {
    float: right;
}
.source_name {
    width: 3.5rem;
}
.source_item {
    padding: .25rem;
    border-bottom: .05rem solid #eee;
}
.source_empty {
    text-align: center;
    color: #999;
}
.show_explain {
    margin-left: .5rem;
    font-size: .6rem;
    color: #f22;
}
.explain_title {
    padding: .5rem;
    color: #999;
    border-bottom: .05rem solid #eee;
}
.explain_box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 4rem;
    padding: .5rem;
}
.codemirror_editor {
    display: flex;
    flex-direction: column;
    height: 11.8rem;
}
.codemirror_editor_fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: auto;
    z-index: 9999;
}
.codemirror {
    flex: 1;
    height: 1%;
}
.codemirror_nav {
    display: flex;
    line-height: 1rem;
    padding: 0 .5rem;
    background: #FFF;
}
.btn_nav {
    padding: .5rem;
}
.max_show {
    display: flex;
}
.btn-save {
    margin: .25rem;
}
.btn_delete {
    display: inline-block;
    text-align: center;
    color: #FFF;
    border-radius: 50%;
    width: 1rem;
    height: 1rem;
    font-weight: 700;
    line-height: 1rem;
    background: #f22;
}
.btn_delete:active {
    background: #f88;
}
.remove_msg,
.remove_btn {
    margin: .5rem;
}
.remove_title {
    padding: .5rem;
    border-bottom: 0.05rem solid #eee;
}
.remove_msg {
    margin: 1rem .5rem;
} 
.flex-delete {
    width: 1rem;
}
.mar {
    margin-top: 0.25rem;
}
.flex-1 {
    flex: 1;
    text-align: center;
}
.flex-2 {
    flex: 2;
    text-align: center;
}
.props {
    margin: 0.15rem;
    height: 1.2rem;
}
.use_props .btn {
    display: inline-block;
    margin: .25rem;
}
</style>
<style>
 .CodeMirror {
    height: 100% !important;
}
</style>



