<template>
  <div>
    <transition name="modal-opacity">
      <div v-if="show" class="v-modal-overlay"></div>
    </transition>
    <transition name="modal-zoom">
      <div v-if="show" class="v-modal-p">
        <div class="v-modal" @click.stop>
          <div class="v-modal-header">
            <slot name="title"></slot>
          </div>
          <div class="v-modal-content">
            <slot name="content"></slot>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  props: ["show"],
  computed: {},
  methods: {}
};
</script>

<style scoped>
.v-modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  background: #000;
  z-index: 10000;
}
.v-modal-p {
  display: flex;
  align-items: center;
  z-index: 10020;
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0;
  right: 0;
}
.v-modal {
  width: 90%;
  z-index: 10080;
  border-radius: 0.1rem;
  overflow: hidden;
  text-align: center;
  background: #FFF;
  margin-left: 5%;
  font-size: 0.75rem;
  line-height: 1.1rem;
  display: flex;
  position: relative;
  flex-direction: column;
  max-height: 100%;
}
/*--Vue--弹窗动画效果*/
.modal-opacity-enter-active {
  animation: opacityIn 0.2s ease;
  -webkit-animation: opacityIn 0.2s ease;
  -moz-animation: opacityIn 0.2s ease;
}
.modal-opacity-leave--active {
  animation: opacityOut 0.8s ease;
  -webkit-animation: opacityOut 0.8s ease;
  -moz-animation: opacityOut 0.8s ease;
}
.modal-zoom-enter-active {
  animation: zoomIn 0.4s ease;
  -webkit-animation: zoomIn 0.4s ease;
  -moz-animation: zoomIn 0.4s ease;
}
.modal-zoom-leave-active {
  animation: zoomOut 0.3s ease-out;
  -webkit-animation: zoomOut 0.3s ease-out;
  -moz-animation: zoomOut 0.3s ease-out;
}
.fade-enter-active,
.fade-leave-active {
  opacity: 1;
  transition: all 0.2s linear;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}

@keyframes zoomIn {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes zoomIn {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@-moz-keyframes zoomIn {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes zoomOut {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(0.5);
  }
}
@-webkit-keyframes zoomOut {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(0.5);
  }
}
@-moz-keyframes zoomOut {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(0.5);
  }
}

@keyframes opacityIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.6;
  }
}
@-webkit-keyframes opacityIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.6;
  }
}
@-moz-keyframes opacityIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.6;
  }
}
@keyframes opacityOut {
  0% {
    opacity: 0.6;
  }
  80% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes opacityOut {
  0% {
    opacity: 0.6;
  }
  80% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
  }
}
@-moz-keyframes opacityOut {
  0% {
    opacity: 0.6;
  }
  80% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
  }
}
</style>


