<template>
    <div>
		<div class="source_body" v-for="item in source" :key="item.name">
			<div class="source_title">{{item.title}} - {{item.name}}</div>
				<div class="source_list">
					<div class="btn source_item" v-for="_item in item.action" :key="_item.name" @click="addSource(item, _item)">
						{{_item.title}}
					</div>
				</div>
		</div>
    </div>
</template>

<script>

export default {
	components: {
	},
	computed: {
		source() {
			return this.$store.state.source;
		}
	},
	methods: {
		addSource(item1, item2) {
			this.$store.commit('addSource', {item1, item2});
			this.$emit('hide-model');
		}
	}
};
</script>

<style scoped>
.source_body {
	color: #333;
	text-align: left;
	border-bottom: .05rem solid #eee;
}
.source_title {
	padding: 0 .5rem;
	line-height: 1.8rem;
}
.source_list {
    display: flex;
    flex-wrap: wrap;
	align-content: flex-start;
	padding: .5rem;
	padding-top: 0;
	line-height: 1.6rem;
	/* background: rgba(0, 0, 0, .15); */
}
.source_item {
	margin: .25rem;
	/* padding-left: .25rem; */
}

</style>



